@import url(https://fonts.googleapis.com/css?family=Roboto+Slab);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navbar{
    background: #333;
}

.nav-link{
    color: #fff;
    font-size: 16px;
    font-family: 'Roboto Slab', serif;
}

.active .nav-link{
    color: #eb4947;
}

.nav-link:hover{
    color: #eb4947;
}

.navbar-toggler .fa-bars {
    font-size: 24px;
    color: #fff;
}

.navbar-toggler .fa-times{
    font-size: 26px;
    color: #fff;
}

.fa-bars{
    font-size: 18px;
}



@media screen and (max-width: 1200px) and (min-width: 992px) {
    .nav-link{
        font-size: 12px;
    }
}

@media screen and (max-width: 992px) {
    .nav-item:last-of-type{
        display: none;
    }
    .navbar{
        position: absolute!important;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 2;
    }
}

@media screen and (max-width: 350px) {
    .navbar-brand img{
        width:200px;
    }
}
.hero-image{
    background: url(/LTRestaurant/static/media/banner.de14dd4d.jpg) center no-repeat;
    height: 550px;
    background-size: cover;
    font-family: 'Roboto Slab', serif;
}

.banner-content{
    padding: 120px 0;    
    text-align: left;
}

.banner-content h1{
    color: #fff;
    font-size: 60px;
    font-weight: bold;
}

.banner-content h2{
    color: #fff;
    font-size: 50px;
}
.banner-content button{
    background: none;
    outline: none;
    border: 2px solid #fff;
    color: #fff;
    padding: 10px 20px;
    margin: 20px 0; 
}

.banner-content button:hover{
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    border: 2px solid #eb4947;
    background: #eb4947;
}

@media screen and (max-width: 1200px) and (min-width: 992px){
    .hero-image{
        height: 400px;
    }    
    .banner-content{
        padding: 80px 0;
    }
    .banner-content h1{
        font-size: 40px;
    }
    
    .banner-content h2{
        font-size: 30px;
    }
    .banner-content button{
        padding: 7px 14px;
        margin: 25px 0; 
    }
}

@media screen and (max-width: 992px) and (min-width: 768px){
    .hero-image{
        height: 500px;
    }
    .banner-content{
        text-align: center;
        padding: 150px 0;
    }
    .banner-content h1{
        font-size: 50px;
    }
    
    .banner-content h2{
        font-size: 40px;
    }
    .banner-content button{
        padding: 8px 16px;
        margin: 30px 0; 
    }    
}

@media screen and (max-width: 768px) {
    .hero-image{
        height: 400px;
    }
    .banner-content{
        text-align: center;
        padding: 130px 0;
    }
    .banner-content h1{
        font-size: 32px;
    }

    .banner-content h2{
        font-size: 24px;
    }
    .banner-content button{
        padding: 6px 12px;
        margin: 30px 0; 
    }        
}

@media screen and (max-width: 500px) {
    .hero-image{
        height: 550px;
    }    
    .banner-content{
        padding: 200px 0;        
    }
    .banner-content h1{
        font-size: 30px;
    }
    .banner-content h2{
        font-size: 24px;
    }
}
.Section{
    font-family: 'Roboto Slab', serif;
    height: 500px;
    margin: 20px 0;
}

.section-head h2{
    padding: 10px 0;
    color: #000;
    font-weight: 600;
    padding: 20px 0;
}

.list-row{
    margin-top: 20px;
    box-shadow: 0 0 5px #000;
}

.col-lg-4{
    margin: 10px auto;
}

.box{
    margin: 10px;
    padding: 10px 20px;
    border: 2px solid #000;
}

.box img{
    width: 275px;
}

.amount{
    margin: 5px 0;
}
.amount span{
    color: #247e94;
    font-weight: 600;
}

.type{
    font-weight: 600;
    margin-bottom: 5px;
}

.box button{
    color: #fff;
    background: #eb4947;
    border: none;
    outline: none;
}

@media screen and (max-width: 1200px) and (min-width: 992px){
    .box img{
        width: 220px;
    }    
}

@media screen and (max-width: 992px) and (min-width: 768px){
    .Section{
        height: auto;
        margin: 20px 20% 50px 20%;
    }       
    .box{
        margin: 20px 10px;
    }
    .col-lg-4{
        margin-top: 10px;
    }
    .box img{
        width: 100%;
        height: 300px
    }
    .amount, .type{
        margin: 10px 0;
        font-size: 18px!important;
    }
    .box button{
        margin: 10px 0;
        padding: 10px;        
    }   
}

@media screen and (max-width: 768px) {
    .Section{
        height: auto;
        margin: 20px 10% 50px 10%;
    }    
    .box{
        margin: 20px 10px;
    }
    .section-head h2{
        font-size: 24px;        
    }
    .col-lg-4{
        margin-top: 10px;
    }
    .box img{
        width: 100%;
        height: 200px;
    }
    .amount, .type{
        margin: 10px 0;
        font-size: 18px!important;
    }
    .box button{
        margin: 10px 0;
        padding: 5px;        
    }   
}

@media screen and (max-width: 500px) {
    .Section{
        height: auto;
        margin: 20px 5px 50px 5px;
    }
    .box{
        margin: 10px 0px;
        padding: 0 0 10px 0;
    }
    .section-head h2{
        font-size: 24px;        
    }
    .col-lg-4{
        margin-top: 5px;
    }
    .box img{
        width: 100%;
        height: 200px;
    }
    .amount, .type{
        margin: 10px 0;
        font-size: 16px!important;
    }
    .box button{
        margin: 5px 0;
        font-size: 12px;
    }          
}
.Footer{
    height: auto;
    background: #333;
    font-family: 'Roboto Slab', serif;
}

.footer-content{
    padding: 70px 0 20px 0;
    margin: 0 10%;
    color: #fff;
    text-align: left;
}

.footer-box{
    margin: 0 10px;
}

.highlight{
    color: #eb4947;
}

.footer-box h3{
    margin-bottom: 15px;
}

.footer-line p:not(:last-of-type):after{
    content: ' ';
    display: block;
    width: 100%;
    background: #fff;
    height: 1px;
    margin-top: 10px;
}

@media screen and (max-width: 1200px) and (min-width: 992px){
    .footer-content{
        margin: 0 5%;
    }
}

@media screen and (max-width: 992px) and (min-width: 768px){
    .footer-content{
        margin: 0 5px;        
    }    
}

@media screen and (max-width: 500px) {
    .footer-content{
        margin: 0 5px;
        padding-top: 40px;
    }
    .footer-box h3{
        font-size: 20px;        
        margin-bottom: 25px;
    }
    .footer-box p{
        font-size: 15px;
    }
    .footer-box{
        margin: 25px 0px;
    }
    .footer-line p:not(:last-of-type):after{
        height: 1px;        
    }
}
